.MyModal {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background: rgba(0, 0, 0, 0.8);
}

.MyModalContent {
    padding: 25px;
    background: var(--darkest);
    border-radius: 20px;
    min-width: 250px;
}

.MyModal.active{
    display: flex;
    justify-content: center;
    align-items: center;
}